body {
	font-family: 'Montserrat', sans-serif;
}

html {
	scroll-behavior: smooth;
}

/* Scroll top button */

#page_top {
	width: 50px;
	height: 50px;
	margin: 10px;
	border-radius: 5px;
	position: fixed;
	right: 20px;
	bottom: 20px;
	background: #a9a9a9;
	opacity: 0.4;
	z-index: 10;
}

#page_top a {
	position: relative;
	display: block;
	width: 50px;
	height: 50px;
	text-decoration: none;
}

#page_top a::before {
	font-family: 'Font Awesome 5 Free';
	font-weight: 900;
	content: '\f106';
	font-size: 25px;
	color: #000000;
	position: absolute;
	width: 25px;
	height: 25px;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	margin: auto;
	text-align: center;
}

/* Wrapper */

.wrapper {
	display: flex;
	flex-wrap: wrap;
}

/* Mobile version */
@media only screen and (max-width: 400px) {
	body {
		background-color: #ffffff;
	}
}

/* Desktop version */
@media only screen and (min-width: 961px) {
	.wrapper {
		min-width: 960px;
		margin: 0 auto;
	}
}
