.section {
	width: 100%;
	height: 300px;
	display: flex;
	justify-content: center;
	align-items: center;
}

/* Hero-banner */

/* Background pattern credit to Toptal Subtle Patterns */
.Hero-banner {
	background: white url(//subtlepatterns.com/patterns/photography.png) repeat 0
		0;
	width: 100%;
	margin: 0;
	text-align: center;
	min-height: 600px;
	padding-top: 120px;
	box-sizing: border-box;
	-webkit-animation: slide 20s linear infinite;
}

@-webkit-keyframes slide {
	from {
		background-position: 0 0;
	}
	to {
		background-position: -400px 0;
	}
}

.about-image {
	padding: 5px;
	margin: 10px;
}

.about-image img {
	width: 30%;
	height: auto;
	transition: all ease 500ms;
}

.about-image img:hover {
	transform: scale(1.1);
	opacity: 1.3;
}

/* Desktop navigation */

@media only screen and (min-width: 961px) {
	.hero-banner {
		height: 500px;
	}
	.about-image img {
		width: 20%;
		height: auto;
	}

	.about-text h2 {
		padding: 20px;
		margin: 0 80px;
	}

	.about-text p {
		padding: 0px 80px;
		margin: 0 80px;
	}
}

@media only screen and (min-width: 401px) {
	header {
		height: 500px;
	}
	.about-image img {
		width: 15%;
		height: auto;
	}

	.about-text h2 {
		padding: 20px;
		margin: 0 80px;
	}

	.about-text p {
		margin: 0 80px;
	}
}
