.section {
  width: 100%;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Navigation bar */

header {
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background: #b2d6ff;
  color: #000000;
  position: fixed;
  z-index: 9999;
  box-shadow: 0 2px 2px -2px rgba(0, 0, 0, 0.2);
}

.Nav {
  width: 70%;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: row;
  position: static;
  margin: 0;
  background-color: transparent;
  visibility: visible;
}

.navlink {
  text-align: center;
  list-style: none;
  padding: 5px;
  margin: 10px;
}

.navlink a {
  color: #000000;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  padding: 6px;
}

.navlink a:hover {
  color: #a9a9a9;
}

/* Desktop navigation */
@media only screen and (min-width: 401px) {
  header {
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    background: #b2d6ff;
    color: #000000;
  }

  .Nav {
    width: 70%;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: row;
    position: static;
    margin: 0;
    background-color: transparent;
    visibility: visible;
  }
  .navlink {
    list-style: none;
    margin: 0;
  }

  .navlink a {
    color: #000000;
    text-decoration: none;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 1rem;
    cursor: pointer;
    padding: 6px;
  }

  .navlink a:hover {
    color: #a9a9a9;
  }

  .burger {
    font-size: 1.2em;
    display: none;
  }
}
