.section {
	width: 100%;
	height: 500px;
	display: flex;
	justify-content: center;
	align-items: center;
}

/* Skills Education Work containers */

.Card-1 {
	background-color: #ffffff;
	height: 400px;
	margin-bottom: 10px;
}

.Card-2 {
	background-color: #ffffff;
	height: 400px;
	margin-bottom: 10px;
}

.Card-3 {
	background-color: #ffffff;
	height: 400px;
	margin-bottom: 10px;
}

.box {
	margin: 2em;
	border: solid 3px #000000;
	font-weight: bold;
	background-color: #b2d6ff;
	box-shadow: 8px 5px 0 black;
}

.Card-container {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	align-content: center;
	height: 100%;
	z-index: 0;
}

/* Modal containers */

.Modal {
	position: fixed;
	z-index: 9999;
	padding: 0.5em;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	overflow: auto;
	background-color: #fffafa;
	background-color: #b2d6ff;
}

.Modal h2 {
	margin: 5px;
	padding: 5px 5px 5px 0;
}

.Modal p {
	margin: 5px;
	padding: 0;
}

.Modal-content {
	background-color: #fefefe;
	margin: 50px auto auto auto;
	padding: 20px;
	border: solid 3px #000000;
	width: 90%;
	height: auto;
	word-break: break-word;
	border-radius: 5px;
}

.cardBtn {
	width: 120px;
	display: flex;
	justify-content: center;
	padding: 1em;
	text-decoration: none;
	color: #000000;
	border: solid 3px #000000;
	border-radius: 5px;
	transition: 0.4s;
	cursor: pointer;
	background-color: #fffafa;
	font-size: 1rem;
	border-radius: 10px;
	transition: all ease 500ms;
	box-shadow: 5px 5px 0 black;
	text-transform: uppercase;
}

.cardBtn:hover {
	background: #f4d6db;
	color: #000000;
	cursor: pointer;
	transform: scale(1.1);
	opacity: 1.3;
}

.close {
	position: absolute;
	right: 45px;
	top: 15px;
	color: #aaaaaa;
	font-size: 2em;
	font-weight: bold;
}

.close:hover,
.close:focus {
	color: #000;
	text-decoration: none;
	cursor: pointer;
}

/* Skills - pop-up window */

#skills {
	position: relative;
	height: 100%;
	width: 100%;
	z-index: 1;
}

#skills::before {
	content: '';
	background: linear-gradient(90deg, #b2d6ff, #b3dbd3, #f4d6db);
	position: absolute;
	height: 100%;
	width: 100%;
	opacity: 0.6;
	border-radius: 5px;
}

#skill-modal {
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
}

#skill-modal a {
	text-decoration: none;
	color: #000000;
	font-weight: bold;
	padding: 20px 0 20px 0;
}

#skill-modal h2 {
	margin: 5px;
	padding: 5px;
}

#skill-modal ul li {
	list-style-type: none;
	padding: 5px;
}

#skill-modal ul li:before {
	content: '✓';
}

