/* Mobile version */
@media only screen and (max-width: 400px) {
	body {
		background-color: #ffffff;
	}
}

/* Tablet version */
@media only screen and (min-width: 401px) and (max-width: 960px) {
	.Card-1,
	.Card-2,
	.Card-3 {
		width: 33.33%;
	}

	.hero-banner {
		height: 400px;
	}
}

/* Desktop version */
@media only screen and (min-width: 961px) {
	.Card-1,
	.Card-2,
	.Card-3 {
		width: 33.33%;
	}

	.modal-content {
		width: 70%;
		height: auto;
	}
}

/* Desktop navigation */
@media only screen and (min-width: 401px) {
	header {
		width: 100%;
		height: 100px;
		display: flex;
		align-items: center;
		justify-content: space-around;
		background: #b2d6ff;
		color: #000000;
	}
}
