@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
	font-family: 'Montserrat', sans-serif;
}

html {
	scroll-behavior: smooth;
}

/* Scroll top button */

#page_top {
	width: 50px;
	height: 50px;
	margin: 10px;
	border-radius: 5px;
	position: fixed;
	right: 20px;
	bottom: 20px;
	background: #a9a9a9;
	opacity: 0.4;
	z-index: 10;
}

#page_top a {
	position: relative;
	display: block;
	width: 50px;
	height: 50px;
	text-decoration: none;
}

#page_top a::before {
	font-family: 'Font Awesome 5 Free';
	font-weight: 900;
	content: '\f106';
	font-size: 25px;
	color: #000000;
	position: absolute;
	width: 25px;
	height: 25px;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	margin: auto;
	text-align: center;
}

/* Wrapper */

.wrapper {
	display: flex;
	flex-wrap: wrap;
}

/* Mobile version */
@media only screen and (max-width: 400px) {
	body {
		background-color: #ffffff;
	}
}

/* Desktop version */
@media only screen and (min-width: 961px) {
	.wrapper {
		min-width: 960px;
		margin: 0 auto;
	}
}

.section {
  width: 100%;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Navigation bar */

header {
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background: #b2d6ff;
  color: #000000;
  position: fixed;
  z-index: 9999;
  box-shadow: 0 2px 2px -2px rgba(0, 0, 0, 0.2);
}

.Nav {
  width: 70%;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: row;
  position: static;
  margin: 0;
  background-color: transparent;
  visibility: visible;
}

.navlink {
  text-align: center;
  list-style: none;
  padding: 5px;
  margin: 10px;
}

.navlink a {
  color: #000000;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  padding: 6px;
}

.navlink a:hover {
  color: #a9a9a9;
}

/* Desktop navigation */
@media only screen and (min-width: 401px) {
  header {
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    background: #b2d6ff;
    color: #000000;
  }

  .Nav {
    width: 70%;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: row;
    position: static;
    margin: 0;
    background-color: transparent;
    visibility: visible;
  }
  .navlink {
    list-style: none;
    margin: 0;
  }

  .navlink a {
    color: #000000;
    text-decoration: none;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 1rem;
    cursor: pointer;
    padding: 6px;
  }

  .navlink a:hover {
    color: #a9a9a9;
  }

  .burger {
    font-size: 1.2em;
